import { useEffect, useState } from 'react'
import ReactGA from 'react-ga'

import { darkTheme, lightTheme, Theme } from '../../lib'

export const useThemeSwitch = (): [Theme, () => Theme] => {
  const [currentTheme, setCurrentTheme] = useState<Theme>(lightTheme)

  useEffect(() => {
    if (localStorage) {
      const loadedTheme = loadTheme()
      setCurrentTheme(loadedTheme)
    }
  }, [])

  useEffect(() => {
    const theme = currentTheme === lightTheme ? 'light' : 'dark'
    localStorage.setItem('currentTheme', theme)

    ReactGA.event({
      category: 'Theme',
      action: `Switched to ${theme} theme`,
    })
  }, [currentTheme])

  const toggleTheme = () => {
    if (currentTheme === lightTheme) {
      setCurrentTheme(darkTheme)
      return darkTheme
    } else {
      setCurrentTheme(lightTheme)
      return lightTheme
    }
  }

  return [currentTheme, toggleTheme]
}

const loadTheme = () => {
  if (localStorage.getItem('currentTheme') !== undefined) {
    return localStorage.getItem('currentTheme') === 'dark' ? darkTheme : lightTheme
  }

  return Math.random() < 0.5 ? darkTheme : lightTheme
}
